import mapboxgl from "mapbox-gl/dist/mapbox-gl.js";
import RulerControl from "mapbox-gl-controls/lib/ruler";
import MapboxGeocoder from "@mapbox/mapbox-gl-geocoder";

mapboxgl.accessToken =
  "pk.eyJ1IjoiZGV2aW5wZm9mZiIsImEiOiJjaXZiamlqZGowMHNsMnltcWQ1bjhpZDBwIn0.MP_hYbYPay46jp3T_pzw8w";
var map = new mapboxgl.Map({
  container: "map",
  style: {
    version: 8,
    glyphs: "mapbox://fonts/mapbox/{fontstack}/{range}.pbf",
    sources: {
      "raster-tiles": {
        type: "raster",
        tiles: ["https://mt1.google.com/vt/lyrs=y&x={x}&y={y}&z={z}"],
        tileSize: 256,
      },
    },
    layers: [
      {
        id: "simple-tiles",
        type: "raster",
        source: "raster-tiles",
        minzoom: 1,
        maxzoom: 22,
      },
    ],
  },
  center: [-87.5, 38.7],
  zoom: 9,
  minZoom: 1,
  maxZoom: 22,
});

map.on("load", async () => {
  const mappoints = await (
    await fetch(
      "https://raw.githubusercontent.com/devinpfoff/geojson_testing/master/mappoints.geojson"
    )
  ).json();

  const conductors = await (
    await fetch(
      "https://raw.githubusercontent.com/devinpfoff/geojson_testing/master/conductors.geojson"
    )
  ).json();

  map.addSource("conductors", {
    type: "geojson",
    data: conductors,
  });

  map.addLayer({
    id: "conductors",
    type: "line",
    source: "conductors",
    layout: {},
    paint: {
      "line-color": [
        "match",
        ["get", "m"],
        "Overhead",
        "#dbe059",
        "Underground",
        "#98e059",
        "#fff",
      ],
      "line-width": ["match", ["get", "c"], "Primary", 6, "Secondary", 3, 3],
    },
    minzoom: 13,
  });

  map.addSource("mappoints", {
    type: "geojson",
    data: mappoints,
  });

  map.addLayer({
    id: "mappoints",
    type: "circle",
    source: "mappoints",
    layout: {},
    paint: {
      "circle-color": [
        "match",
        ["get", "m"],
        "Enclosure",
        "#fff",
        "Junction Box",
        "#fff",
        "Marker",
        "#4fb700",
        "Non-Utility Facility",
        "#dbe059",
        "Pad",
        "#96337d",
        "Pedestal",
        "#59e0d7",
        "Pole",
        "#fff",
        "#000",
      ],
      "circle-opacity": 0.8,
      "circle-radius": 8,
      "circle-stroke-color": "#000",
      "circle-stroke-width": 1,
    },
    minzoom: 13,
  });

  function MappointsGeocoder(query) {
    var matchingFeatures = [];
    for (var i = 0; i < mappoints.features.length; i++) {
      var feature = mappoints.features[i];
      if (
        feature.properties.e.toLowerCase().search(query.toLowerCase()) !== -1
      ) {
        feature["place_name"] = feature.properties.e;
        feature["center"] = feature.geometry.coordinates;
        matchingFeatures.push(feature);
      }
    }
    return matchingFeatures;
  }

  map.addControl(
    new MapboxGeocoder({
      accessToken: mapboxgl.accessToken,
      mapboxgl: mapboxgl,
      clearAndBlurOnEsc: true,
      placeholder: "Pole or Address...",
      localGeocoder: MappointsGeocoder,
      countries: "us",
      bbox: [-88.121338, 37.692514, -86.693115, 39.732538],
    }),
    "top-left"
  );

  map.addControl(
    new RulerControl({
      fontSize: 16,
      mainColor: "#bffbfc",
      secondaryColor: "#141414",
      fontHalo: 3,
      units: "miles",
      labelFormat: (n) => `${n.toFixed(2)} mi`,
    }),
    "top-left"
  );
});

map.on("click", "mappoints", function (e) {
  var coordinates = e.features[0].geometry.coordinates.slice();
  var description = e.features[0].properties.e;

  while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
    coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
  }

  function mapsSelector(x, y) {
    if (
      /* if we're on iOS, open in Apple Maps */
      navigator.platform.indexOf("iPhone") != -1 ||
      navigator.platform.indexOf("iPad") != -1 ||
      navigator.platform.indexOf("iPod") != -1
    )
      return `maps://maps.google.com/maps?daddr=${y.toFixed(6)},${x.toFixed(
        6
      )}&amp;ll=`;
    /* else use Google */ else
      return `https://maps.google.com/maps?daddr=${y.toFixed(6)},${x.toFixed(
        6
      )}&amp;ll=`;
  }

  new mapboxgl.Popup()
    .setLngLat(coordinates)
    .setHTML(
      `
        <table>
          <tr><td colspan="2"><b>Map Loc: </b>${
            e.features[0].properties["e"]
          }</td></tr>
          <tr><td colspan="2"><b>Type: </b>${
            e.features[0].properties["m"]
          }</td></tr>
          <tr><td colspan="2"><b>County: </b>${
            e.features[0].properties["c"]
          }</td></tr>
          <tr><td colspan="2"><b>Township: </b>${
            e.features[0].properties["t"]
          }</td></tr>
          <tr><td colspan="2"><button class="btn" onClick='window.open("${mapsSelector(
            e.features[0].geometry.coordinates[0],
            e.features[0].geometry.coordinates[1]
          )}")'>Directions</button></td></tr>
        </table>
      `
    )
    .addTo(map);
});

map.on("mouseenter", "mappoints", function () {
  map.getCanvas().style.cursor = "pointer";
});
